@import url(https://fonts.googleapis.com/css?family=Open+Sans:300);

html,
body,
#container {
	height: 100%;
	width: 100%;
	font-family: 'Open Sans', sans-serif;
	color: #FFF;
	margin: 0;
	padding: 0;
}

.content {
	width: 100%;
}

.datetime {
	width: 100%;
	z-index: 1000;
	padding: 10px;
	position: relative;
	text-shadow: 0 0 20px RGBA(0, 0, 0, 0.9);
	background: RGBA(0, 0, 0, 0.5);
}

.datetime__time {
	font-size: 4em;
}

.datetime__date {
	font-size: 1em;
}

.datetime__hours {
	margin: 2px;
}

.datetime__minutes {
	margin: 2px;
}

.datetime__seconds-ampm {
	display: inline-block;
	vertical-align: middle;
	margin-left: 5px;
}

.datetime__seconds {
	font-size: .5em;
	display: block;
	border-bottom: 5px solid;
}

.datetime__ampm {
	font-size: .5em;
	text-transform: uppercase;
	display: block;
}

.datetime__month {
	margin: 5px;
	margin-right: 10px;
}

.datetime__day {
	margin: 5px;
	margin-right: 0;
}

.datetime__year {
	margin: 5px;
	margin-left: 10px;
}

.image {
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	background-size: cover;
}

.image-container {
	display: flex;
	height: 100%;
	align-items: center;
	text-align: center;
	flex-wrap: wrap;
}

.image-container__wrapper {
	background: RGBA(0, 0, 0, 0.5);
	text-shadow: 0 0 20px RGBA(0, 0, 0, 0.9);
	display: inline-block;
}

.list {
	display: inline-block;
	position: relative;
	z-index: 10;
	padding: 40px;
	text-align: left;
	vertical-align: top;
}

.list h2 {
	margin-top: 0;
	padding-top: 0;
}

.list:not(:last-child) {
	border-right: 1px solid #fff;
}

.list-container {
	z-index: 1000;
	flex: 0 0 100%;
}

.list ul {
	list-style: none;
	padding: 0;
	padding-bottom: 20px;
	margin: 0;
	margin-bottom: 20px;
	border-bottom: 5px solid #FFF;
}

.list input[type=checkbox] {
	margin-right: 10px;
	margin-top: 5px;
	vertical-align: top;
}

.list button {
	background: #FFF;
	outline: none;
	border-radius: 0;
	border: none;
	margin: 0 5px;
	text-transform: uppercase;
}

.weather {
	z-index: 1000;
	color: #FFF;
	padding: 40px;
	flex: 0 0 100%;
	text-align: center;
	border-bottom: 1px solid #fff;
	min-height: 100px;
}

.weather__temp,
.weather__day {
	vertical-align: middle;
	text-shadow: 0 0 20px RGBA(0, 0, 0, 0.9);
}

.weather__icon {
	height: 50px;
	width: 50px;
	vertical-align: middle;
}

.weather--current {
	display: inline-block;
}

.weather--forecast {
	display: inline-block;
	border-left: 1px solid #FFF;
	margin-left: 20px;
}

.weather--forecast__item {
	display: inline-block;
	margin: 0 20px;
}

